import gtRequest from "./gtRequest.js";

//ws
let isOpenSocket = false
//
let heartBeatDelay = 50000
let heartBeatInterval = null
//
let heartBeatText = "gtPING"
//
let reconnectTimes = 10
let reconnectInterval = null
//
let reconnectDelay = 3000

// var environment = uni.getStorageSync('environment');
// var url = '';
// if (environment == 'prod') {
// 	url = 'wss://orderapi.sansongkeji.com/wss';
// } else {
// 	url = 'wss://sansong.yingjiezj.com/wss';
// }

// let wsUrl = url

let socketTask = null

//onClosereconnect
//
let canReconnect = false

//
//init socketTask
//completeClose socketTask
//socketTaskuniappsocketTask api
let ws = {
	socketTask: null,
	init,
	completeClose
}
const innerAudioContext = uni.createInnerAudioContext();

let companyInfo = uni.getStorageSync('companyInfo');

let logisticsId = companyInfo.logistics_id;

function init(wsUrl) {
	socketTask = uni.connectSocket({
		url: wsUrl,
		complete: () => {}
	})
	console.log(socketTask);

	socketTask.onOpen((res) => {
		console.log("ws")
		clearInterval(heartBeatInterval)
		clearInterval(reconnectInterval)
		isOpenSocket = true
		canReconnect = true


		//
		heartBeat()
	})
	socketTask.onMessage((res) => {
		//onMessage
		console.log(res);
		let gt = this;


		// 	var pages = getCurrentPages();
		// 	var currentRoute = pages[pages.length-1].route;
		// 	console.log(currentRoute);


		var data = res.data;
		// console.log(data);
		var obj = JSON.parse(data);
		var type = obj.type;


		// 	uni.setStorageSync('client_id',obj.client_id);
		if (type == 'init') {
			var url = "/api/applogin/bind_client_id";
			var data = {
				client_id: obj.client_id,
			};
			uni.setStorageSync('clientId',obj.client_id);

			gtRequest.post(url, data).then(res => {

			});
		}

		// return false;
		var audioStatus = uni.getStorageSync('audioStatus');
		if (!audioStatus) {
			return false;
		}



		if (type == 'order_new_order') {
			var orderInfo = obj.data;
			// console.log(orderInfo);
			var orderInfo = orderInfo.order_info;
			// console.log(orderInfo);
			// return false;
			innerAudioContext.stop();

			// innerAudioContext.autoplay = true;
			innerAudioContext.src =
				'https://baohusan-uisource.oss-cn-shanghai.aliyuncs.com/mp-transport/index/new_order.mp3';
			innerAudioContext.play();
		}

		if (type == 'order_rob_result') {
			var orderInfo = obj.data;
			// console.log(orderInfo);
			var orderInfo = orderInfo.order_info;
			// console.log(orderInfo);
			// return false;

			if (orderInfo.logistics_id == logisticsId) {
				innerAudioContext.stop();
				innerAudioContext.src =
					'https://baohusan-uisource.oss-cn-shanghai.aliyuncs.com/mp-transport/index/seizeOrderSuccess.mp3';
				innerAudioContext.play();
			}

		}

		// if (type == 'order_receive_success') {
		// 	var orderInfo = obj.data;
		// 	// console.log(orderInfo);
		// 	var orderInfo = orderInfo.order_info;
		// 	// console.log(orderInfo);
		// 	// return false;
		// 	innerAudioContext.stop();
		// 	innerAudioContext.src =
		// 		'https://baohusan-uisource.oss-cn-shanghai.aliyuncs.com/mp-transport/index/seizeOrderSuccess.mp3';
		// 	innerAudioContext.play();
		// }
		// 	// uni.showModal({
		// 	// 	title:''+type
		// 	// })
	})
	socketTask.onClose(() => {
		if (isOpenSocket) {
			console.log("ws")
			// uni.showModal({
			// 	title:'ws',
			// 	content:'gtWss',
			// })
		} else {
			console.log("")
			// uni.showModal({
			// 	title:'',
			// 	content:'gtWss',
			// })
		}
		isOpenSocket = false
		if (canReconnect) {
			reconnect()
			canReconnect = false
		}
	})
	ws.socketTask = socketTask
}

function heartBeat() {
	heartBeatInterval = setInterval(() => {
		console.log(heartBeatText)
		send(JSON.stringify(heartBeatText));
	}, heartBeatDelay)
}

function send(value) {
	ws.socketTask.send({
		data: value,
		async success() {
			console.log("")
		}
	});
}

function reconnect() {
	//
	clearInterval(heartBeatInterval)
	//
	if (!isOpenSocket) {
		let count = 0;
		reconnectInterval = setInterval(() => {
			console.log("")
			init();
			count++
			//
			if (count >= reconnectTimes) {
				clearInterval(reconnectInterval)
				console.log("")
			}
		}, reconnectDelay)
	}
}

function completeClose() {
	//
	clearInterval(heartBeatInterval)
	clearInterval(reconnectInterval)
	canReconnect = false
	ws.socketTask.close()
}

module.exports = ws